.s-page-wrapper{ padding:20px; }

.s-page-title{
  font-size:20px;
  margin-bottom:18px;
  color:#0b5c8e;
}

.s-follow-links{
  display:flex;
  flex-direction:column;
  gap:12px;
}

.s-follow-links a{
  background:#fff;
  border:1px solid #d7dee6;
  padding:12px;
  border-radius:6px;
  text-decoration:none;
  color:#0b5c8e;
  font-weight:600;
}

.s-follow-links a:hover{
  background:#eef2f6;
}
