/* ================== ACADEMICS PAGE ================== */

.page-header{
  background:#0b5c8e;
  color:#fff;
  padding:18px 22px;
  border-radius:6px;
  margin-bottom:20px;
}

.page-header h2{
  font-size:20px;
  margin-bottom:4px;
}

.page-header p{
  font-size:13px;
  opacity:0.9;
}

/* GRID */
.academics-grid{
  display:grid;
  grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));
  gap:18px;
}

/* CARD */
.academics-card{
  background:#fff;
  border:1px solid #d7dee6;
  border-radius:8px;
  padding:22px 16px;
  text-align:center;
  cursor:pointer;
  transition:0.2s ease;
}

.academics-card span{
  font-size:34px;
  display:block;
  margin-bottom:10px;
}

.academics-card h4{
  font-size:15px;
  color:#0b5c8e;
  margin-bottom:6px;
}

.academics-card p{
  font-size:13px;
  color:#555;
}

/* HOVER EFFECT */
.academics-card:hover{
  box-shadow:0 6px 14px rgba(0,0,0,0.15);
  transform:translateY(-2px);
}
