.s-page-wrapper{
  padding:20px;
}

.s-page-title{
  font-size:20px;
  margin-bottom:18px;
  color:#0b5c8e;
}

.s-classwork-table{
  width:100%;
  background:#fff;
  border-collapse:collapse;
  border:1px solid #d7dee6;
}

.s-classwork-table th,
.s-classwork-table td{
  padding:12px;
  border:1px solid #d7dee6;
  font-size:13px;
}

.s-classwork-table th{
  background:#0b5c8e;
  color:#fff;
  text-align:left;
}
