.s-page-wrapper{ padding:20px; }

.s-page-title{
  font-size:20px;
  margin-bottom:18px;
  color:#0b5c8e;
}

.s-sports-grid{
  display:grid;
  grid-template-columns:repeat(auto-fit,minmax(220px,1fr));
  gap:16px;
}

.s-sport-card{
  background:#fff;
  border:1px solid #d7dee6;
  padding:16px;
  border-radius:6px;
}

.s-sport-card h4{
  font-size:15px;
  color:#0b5c8e;
  margin-bottom:6px;
}

.s-sport-card p{
  font-size:13px;
  color:#333;
  margin-bottom:6px;
}

.s-sport-card span{
  font-size:13px;
  font-weight:600;
}
