.ta-wrapper{
  background:#eef2f6;
  min-height:100vh;
  padding:16px;
}

/* HEADER */
.ta-header{
  background:#0b5c8e;
  color:#fff;
  padding:16px;
  border-radius:8px;
  margin-bottom:16px;
}

.ta-header h2{
  font-size:18px;
}

.ta-header p{
  font-size:13px;
  opacity:0.9;
}

/* FILTER */
.ta-filter{
  display:flex;
  gap:10px;
  margin-bottom:16px;
}

.ta-filter select{
  flex:1;
  padding:10px;
  border-radius:6px;
  border:1px solid #ccc;
}

.ta-filter button{
  padding:10px 14px;
  background:#0d6ea8;
  color:#fff;
  border:none;
  border-radius:6px;
  cursor:pointer;
  font-weight:600;
}

/* LIST */
.ta-list{
  background:#fff;
  border-radius:8px;
  overflow:hidden;
}

.ta-row{
  display:flex;
  align-items:center;
  padding:12px;
  border-bottom:1px solid #e3e7ee;
}

.ta-row:last-child{
  border-bottom:none;
}

.roll{
  width:30px;
  font-weight:600;
}

.name{
  flex:1;
}

.actions{
  display:flex;
  gap:8px;
}

.actions button{
  width:36px;
  height:36px;
  border:none;
  border-radius:6px;
  color:#fff;
  font-weight:700;
  cursor:pointer;
}

.present{
  background:#9ca3af;
}

.absent{
  background:#9ca3af;
}

.present.active{
  background:#22c55e;
}

.absent.active{
  background:#ef4444;
}

/* FOOTER */
.ta-footer{
  margin-top:16px;
}

.ta-footer button{
  width:100%;
  padding:12px;
  background:#0d6ea8;
  color:#fff;
  border:none;
  border-radius:8px;
  font-size:15px;
  font-weight:600;
}

/* LOADING */
.ta-loading{
  text-align:center;
  padding:20px;
  color:#555;
}

/* RESPONSIVE */
@media(max-width:600px){
  .ta-filter{
    flex-direction:column;
  }
}
