.s-page-wrapper{
  padding:20px;
}

.s-page-title{
  font-size:20px;
  margin-bottom:18px;
  color:#0b5c8e;
}

.s-homework-list{
  display:grid;
  grid-template-columns:repeat(auto-fit,minmax(240px,1fr));
  gap:16px;
}

.s-homework-card{
  background:#fff;
  border:1px solid #d7dee6;
  padding:16px;
  border-radius:6px;
}

.s-homework-card h4{
  font-size:15px;
  color:#0b5c8e;
  margin-bottom:6px;
}

.s-homework-card p{
  font-size:13px;
  color:#333;
  margin-bottom:6px;
}

.s-homework-card span{
  font-size:12px;
  color:#c62828;
  font-weight:600;
}
