/* Page Wrapper */
.tstudents-wrapper {
  padding: 25px;
  background: linear-gradient(135deg, #f0f4ff, #fdfbff);
  min-height: 100vh;
  font-family: "Segoe UI", sans-serif;
}

/* Title */
.page-title {
  text-align: center;
  margin-bottom: 25px;
  font-size: 26px;
  color: #3f51b5;
  font-weight: 700;
}

/* Filter Bar */
.filter-bar {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 25px;
  flex-wrap: wrap;
}

.filter-bar select {
  padding: 10px 14px;
  border-radius: 8px;
  border: 1px solid #c5cae9;
  background: #fff;
  font-size: 14px;
  min-width: 160px;
  transition: all 0.2s ease;
}

.filter-bar select:focus {
  outline: none;
  border-color: #3f51b5;
  box-shadow: 0 0 0 2px rgba(63, 81, 181, 0.15);
}

.filter-bar button {
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  background: linear-gradient(135deg, #3f51b5, #5c6bc0);
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  font-weight: 600;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.filter-bar button:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(63, 81, 181, 0.3);
}

/* Loading / No Data */
.tstudents-wrapper p {
  text-align: center;
  font-size: 15px;
  color: #666;
}

.no-data {
  color: #d32f2f;
  font-weight: 600;
}

/* Students Table */
.students-table {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  border-collapse: collapse;
  background: #ffffff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
}

.students-table thead {
  background: linear-gradient(135deg, #3f51b5, #5c6bc0);
  color: #fff;
}

.students-table th,
.students-table td {
  padding: 14px 12px;
  text-align: center;
  font-size: 14px;
}

.students-table th {
  font-weight: 700;
  letter-spacing: 0.4px;
}

.students-table tbody tr:nth-child(even) {
  background-color: #f6f8ff;
}

.students-table tbody tr:hover {
  background-color: #e8ecff;
  transition: background-color 0.2s ease;
}

.students-table td {
  color: #333;
}

/* Responsive */
@media (max-width: 768px) {
  .students-table th,
  .students-table td {
    padding: 10px 8px;
    font-size: 13px;
  }

  .filter-bar {
    gap: 8px;
  }

  .filter-bar select,
  .filter-bar button {
    width: 100%;
    max-width: 280px;
  }
}
