/* ================== WRAPPER ================== */
.admin-fee-wrapper{
  min-height:100vh;
  background:#eef2f6;
  font-family:"Segoe UI", sans-serif;
  padding:20px;
}

/* ================== FILTER BAR ================== */
.admin-fee-filters{
  display:flex;
  flex-wrap:wrap;
  gap:12px;
  padding:14px;
  background:#fff;
  border-radius:6px;
  margin-bottom:14px;
}

.admin-fee-filters select{
  padding:7px 8px;
  font-size:13px;
  border:1px solid #cfd8e3;
  border-radius:4px;
}

.admin-fee-filters button{
  padding:7px 16px;
  background:#0b5c8e;
  color:#fff;
  border:none;
  border-radius:4px;
  cursor:pointer;
  font-size:13px;
}

.admin-fee-filters button:hover{
  background:#083b5d;
}

/* ================== SUMMARY ================== */
.admin-fee-summary{
  display:grid;
  grid-template-columns:repeat(3,1fr);
  gap:14px;
  margin-bottom:14px;
}

.admin-fee-summary div{
  background:#fff;
  padding:14px;
  border-radius:6px;
  font-size:14px;
  font-weight:600;
  display:flex;
  justify-content:space-between;
}

.admin-fee-summary div:nth-child(1){
  border-left:4px solid #0b5c8e;
}

.admin-fee-summary div:nth-child(2){
  border-left:4px solid #16a34a;
}

.admin-fee-summary div:nth-child(3){
  border-left:4px solid #dc2626;
}

/* ================== TABLE ================== */
.admin-fee-table{
  width:100%;
  border-collapse:collapse;
  background:#fff;
  border-radius:6px;
  overflow:hidden;
}

.admin-fee-table th{
  background:#f1f5f9;
  text-align:left;
  font-size:13px;
  padding:10px;
  border-bottom:1px solid #ddd;
}

.admin-fee-table td{
  padding:10px;
  font-size:13px;
  border-bottom:1px solid #eee;
}

.admin-fee-table tr:nth-child(even){
  background:#fafafa;
}

/* ================== STATUS BUTTON ================== */
.admin-fee-table button{
  padding:4px 10px;
  font-size:12px;
  border:none;
  border-radius:4px;
  cursor:pointer;
  background:#0b5c8e;
  color:#fff;
}

.admin-fee-table button:hover{
  background:#083b5d;
}

/* ================== PAID LABEL (OPTIONAL) ================== */
.paid{
  color:#16a34a;
  font-weight:600;
}

/* ================== RESPONSIVE ================== */
@media(max-width:900px){

  .admin-fee-filters{
    flex-direction:column;
  }

  .admin-fee-summary{
    grid-template-columns:1fr;
  }

  .admin-fee-table{
    font-size:12px;
  }
}
