/* ===== PAGE ===== */
.s-page-wrapper{
  padding:20px;
  background:#f4f7fb;
  display:flex;
  justify-content:center;   /* ðŸ”¥ center */
}

/* ðŸ”¥ INNER CONTAINER (DESKTOP CONTROL) */
.s-attendance-container{
  width:100%;
  max-width:900px;          /* ðŸ‘ˆ desktop me failne se roke */
}

/* ===== TITLE ===== */
.s-page-title{
  font-size:20px;
  margin-bottom:14px;
  color:#0b5c8e;
  text-align:center;
  font-weight:600;
}

/* ===== TODAY STATUS ===== */
.today-status{
  text-align:center;
  margin-bottom:12px;
  font-weight:600;
  font-size:14px;
}

.today-status .present{ color:#2e7d32; }
.today-status .absent{ color:#c62828; }

/* ===== SUMMARY BOXES ===== */
.s-attendance-summary{
  display:grid;
  grid-template-columns:repeat(auto-fit,minmax(150px,1fr));
  gap:14px;
  margin:0 auto 16px;
}

.s-box{
  background:#fff;
  border:1px solid #d7dee6;
  padding:14px;
  border-radius:8px;
  text-align:center;
}

.s-box h4{
  font-size:13px;
  color:#555;
  margin-bottom:6px;
}

.s-box p{
  font-size:20px;
  font-weight:700;
  color:#0b5c8e;
}

.s-box.success p{ color:#2e7d32; }
.s-box.danger p{ color:#c62828; }

/* ===== SECTION TITLE ===== */
.section-title{
  margin:14px 0 8px;
  color:#0b5c8e;
  font-size:14px;
  font-weight:600;
  text-align:center;
}

/* ===== CALENDAR WRAPPER ===== */
.calendar-wrapper{
  background:#fff;
  padding:14px;
  border-radius:10px;
  border:1px solid #d7dee6;
  max-width:420px;      /* ðŸ”¥ calendar chhota */
  margin:0 auto 14px;   /* center */
}

/* ===== CALENDAR HEADER ===== */
.calendar-header{
  display:flex;
  justify-content:space-between;
  align-items:center;
  margin-bottom:10px;
  font-weight:600;
  color:#0b5c8e;
}

.calendar-header button{
  background:#0b5c8e;
  color:#fff;
  border:none;
  padding:4px 10px;
  border-radius:5px;
  cursor:pointer;
}

/* ===== CALENDAR GRID ===== */
.calendar-grid{
  display:grid;
  grid-template-columns:repeat(7,1fr);
  gap:6px;
}

/* ===== DAY CELL ===== */
.cal-day{
  padding:8px 0;
  text-align:center;
  border-radius:6px;
  font-size:13px;
  cursor:pointer;
  background:#f1f3f6;
  transition:0.2s;
}

.cal-day.present{
  background:#d4f7d4;
  color:#1a7f37;
  font-weight:600;
}

.cal-day.absent{
  background:#ffd6d6;
  color:#b42318;
  font-weight:600;
}

.cal-day:hover{
  background:#e2e8f0;
}

/* ===== CLICKED DAY INFO ===== */
.day-info{
  margin-top:10px;
  text-align:center;
  font-size:14px;
  font-weight:600;
}

/* ===== MOBILE ===== */
@media(max-width:600px){
  .s-page-wrapper{ padding:12px; }

  .s-attendance-container{
    max-width:100%;
  }

  .calendar-wrapper{
    max-width:100%;
  }

  .s-page-title{
    font-size:18px;
  }

  .cal-day{
    font-size:12px;
    padding:7px 0;
  }
}
