/* ===== PAGE ===== */
.s-page-wrapper{
  padding:14px;
  background:#f4f7fb;
  min-height:100vh;
}

/* ===== TITLE ===== */
.s-page-title{
  font-size:18px;
  margin-bottom:8px;   /* card ke paas laaye */
  color:#0b5c8e;
  text-align:center;
  font-weight:600;
}


/* ===== CARD (MAIN FIX) ===== */
.result-card{
  max-width:820px;
  margin:0 auto 20px auto;  /* center + neeche thoda gap */
  background:#fff;
  padding:14px;
  border-radius:6px;
  border:1px solid #d7dee6;
}


/* ===== FILTER ===== */
.result-filters{
  display:flex;
  justify-content:flex-end;
  margin-bottom:8px;
}

.result-filters select{
  padding:6px 10px;
  border-radius:4px;
  border:1px solid #cfd8e3;
  font-size:13px;
  background:#fff;
}

/* ===== TABLE WRAPPER ===== */
.table-wrapper{
  overflow-x:auto;
}

/* ===== TABLE ===== */
.s-result-table{
  width:100%;
  border-collapse:collapse;
  border:1px solid #d7dee6;
  font-size:13px;
}

/* ===== HEAD + CELL ===== */
.s-result-table th,
.s-result-table td{
  padding:7px 6px;           /* ðŸ”¥ compact spacing */
  border:1px solid #d7dee6;
  text-align:center;
}

.s-result-table th{
  background:#0b5c8e;
  color:#fff;
  font-weight:600;
}

/* ===== SUBJECT ALIGN ===== */
.s-result-table td:first-child{
  text-align:left;
  padding-left:10px;
}

/* ===== HOVER ===== */
.s-result-table tbody tr:hover{
  background:#f1f6fb;
}

/* ===== MOBILE ===== */
@media(max-width:600px){
  .result-card{
    padding:10px;
  }

  .s-result-table th,
  .s-result-table td{
    font-size:12px;
    padding:6px 4px;
  }

  .s-page-title{
    font-size:16px;
  }
}
