/* ===== GLOBAL FIX ===== */
body{
  margin:0;
  padding:0;
  overflow-x:hidden;   /* ðŸ”¥ mobile left cut fix */
}

/* ===== PAGE ===== */
.s-page-wrapper{
  padding:12px;
  background:#f2f7ff;
  min-height:100vh;
  width:100%;
  max-width:100%;
  box-sizing:border-box;
  overflow-x:hidden;   /* ðŸ”¥ important */
}

/* ===== GRID ===== */
.fee-grid{
  display:grid;
  grid-template-columns:1fr;
  gap:16px;
}

/* ===== CARD ===== */
.fee-card{
  width:100%;
  background:#ffffff;
  border-radius:8px;
  padding:12px;
  border:1px solid #cfdff5;
  box-sizing:border-box;
  overflow-x:auto;     /* ðŸ”¥ table scroll card ke andar */
}

/* ===== SECTION TITLE ===== */
.s-section-title{
  font-size:15px;
  margin-bottom:10px;
  font-weight:600;
  color:#0d47a1;
}

/* ===== TABLE WRAPPER ===== */
.table-wrapper{
  width:100%;
  max-width:100%;
  overflow-x:auto;
  -webkit-overflow-scrolling:touch;
}

/* ===== TABLE ===== */
.s-fee-table{
  width:100%;
  min-width:520px;          /* mobile + desktop balanced */
  border-collapse:collapse;
  border:1px solid #b6d2f5;
}

/* ===== TABLE HEADER ===== */
.s-fee-table th{
  background:#e3f0ff;
  color:#0d47a1;
  padding:8px;
  border:1px solid #b6d2f5;
  font-size:13px;
  white-space:nowrap;
}

/* ===== TABLE CELL ===== */
.s-fee-table td{
  padding:8px;
  border:1px solid #d3e4fb;
  font-size:13px;
  white-space:nowrap;
}

/* ===== STATUS BADGE ===== */
.paid-badge{
  background:#1976d2;
  color:#fff;
  padding:4px 12px;
  border-radius:6px;
  font-size:12px;
  font-weight:600;
}

.unpaid-badge{
  background:#90caf9;
  color:#0d47a1;
  padding:4px 12px;
  border-radius:6px;
  font-size:12px;
  font-weight:600;
}

/* ===== MOBILE ONLY ===== */
@media(max-width:600px){
  .s-fee-table{
    min-width:480px;
  }

  .s-fee-table th,
  .s-fee-table td{
    font-size:12.5px;
    padding:7px;
  }
}

/* ===== SCROLLBAR (optional) ===== */
.table-wrapper::-webkit-scrollbar{
  height:4px;
}
.table-wrapper::-webkit-scrollbar-thumb{
  background:#90caf9;
  border-radius:10px;
}
